import {mapState} from 'vuex'
export default {
	computed:{
		...mapState(['userInfo'])
	},
	data() {
		return {
			email:'',
			password:"",
			disabled:true,
			loading:false,
			codetime:0,
			
		}
	},
	mounted() {
		if(this.userInfo){
			this.email = this.userInfo.email
		}
	},
	watch:{
		email(val){
			this.change();
		},
		password(val){
			this.change();
		},
	},
	methods: {

		// 监听输入框
		change(){
			if(this.email && this.password){
				this.disabled=false;
				return;
			}
			this.disabled=true;
		},
		async getCheckNum(){
			if(this.codetime > 0){ return; }
			// 验证邮箱格式
			let ePattern = /^([A-Za-z0-9_\-\.])+\@([A-Za-z0-9_\-\.])+\.([A-Za-z]{2,4})$/;
			if(!ePattern.test(this.email)){
				uni.showToast({
					title: '请输入正确邮箱格式',
					icon:"none"
				});
				return false;
			}
			// 请求服务器，发送验证码
			let data =await this.$http.post('user/email',{
				email: this.email
			},{
				"content-type":"application/x-www-form-urlencoded"
			})
			
			console.log(data)
			// 发送成功，开启倒计时
			this.codetime=60;
			let timer=setInterval(()=>{
				this.codetime--;
				if(this.codetime < 1){
					clearInterval(timer);
					this.codetime=0;
				}
			},1000);
		},
		// 验证层
		check(){
			if(!this.email || this.email==""){
				uni.showToast({
					title: '邮箱不能为空',
					icon:"none"
				});
				return false;
			}
			
			// 验证邮箱格式
			let ePattern = /^([A-Za-z0-9_\-\.])+\@([A-Za-z0-9_\-\.])+\.([A-Za-z]{2,4})$/;
			if(!ePattern.test(this.email)){
				uni.showToast({
					title: '请输入正确邮箱格式',
					icon:"none"
				});
				return false;
			}
			
			if(!this.password || this.password==""){
				uni.showToast({
					title: '密码不能为空',
					icon:"none"
				});
				return false;
			}
			return true;
		},
		// 提交
		submit(){
			this.loading=true; this.disabled=true;
			if(!this.check()){ this.loading=false; this.disabled=false; return; }
			// 提交服务器
			this.$http.post('user/email',{
				email: this.email,
				code: this.password
			},{
				"content-type":"application/x-www-form-urlencoded"
			}).then(res=>{
				if(res.code==0){
					uni.showToast({
						title:"邮箱绑定成功",
						icon:'success'
					})
					uni.navigateBack({
						delta:1
					})
				}
			})
			this.loading=false; this.disabled=false;
		}
	}
}
